<template>
  <div id="app">
  <!--头部--->
  
    <app-Header :poiInfo="poiInfo"></app-Header>
  

  <!--导航--->
  
    <app-Nav></app-Nav>
  

  <!--导航--->
  <router-view></router-view>
  </div>
</template>

<script>

import Header from './components/header/Header'
import Nav from '@/components/nav/Nav'

export default {
  name: 'App',
  components: {
    "app-Header":Header,
    "app-Nav":Nav,
  },
  data(){
    return{
      poiInfo:{}
    }
  },
  created(){
    fetch("/api/goods")//获取接口
    .then(res=>{
      return res.json()
    })
    .then(response=>{
      // console.log(response)
      if(response.code==0){ //判断当拿到数据后把数据付给poiInfo
        this.poiInfo=response.data.poi_info
        // console.log(this.poiInfo)
      }
    })
  }
}
</script>

<style>
</style>



