<template>
  <div class="goods">
   <!-- 分类列表 -->
   <!-- ref 比较适合绑定这样的标签 v-model 比较适合绑定 input 和select ... -->
   <div class="menu-wrapper" ref="menuScroll">
     <!-- 专场 -->
    <ul class="">
      <li class="menu-item">
        <p class="text">
          <img class="icon" :src="container.tag_icon" v-if="container.tag_icon">
          {{container.tag_name}}
        </p>
      </li>
       <li class="menu-item" v-for="(item, index) in goods" :key="index">
        <p class="text">
          <img class="icon" :src="item.icon" v-if="item.icon">
          {{item.name}}
        </p>
      </li>
    </ul>
   </div>
   <!-- 商品列表 -->
   <div class="foods-wrapper" ref="foodsScroll">
     <ul>
       <!-- 专场 -->
       <li class="container-list">
         <div v-for="(item, index) in container.operation_source_list" :key="index">
            <img :src="item.pic_url" >    <!--获取图片  -->
          
         </div>
        
       </li>
       <!-- 具体分类 -->
       <li v-for="(item,index) in goods" :key="index" class="food-list" >
         <h3 class="title">{{item.name}}</h3>
          
          <!--具体的商品分类  -->
          <ul>
            <li class="fodd-item" v-for="(fodd, index) in item.spus" :key="index">
               <div class="icon" :style="head_bg(fodd.picture)"></div> 
                <div class="content">
                  <h3 class="name">{{fodd.name}}</h3>
                  <p class="desc" v-if="fodd.description">{{fodd.description}}</p>
                  <div class="extra">
                    <span class="saled">{{fodd.month_saled_content}}</span>
                    <span class="praise">{{fodd.praise_content}}</span>
                  </div>
                  <img class="product" :src="fodd.product_label_picture" alt="">
                   <p class="price">
                     <span class="text">￥{{fodd.min_price}}</span>
                     <span class="unit">/{{fodd.unit}}</span>
                  </p>   
                </div>
            </li>
          </ul>

       </li>
     </ul>
   </div>
    
  </div>
</template>

<script>
// import BScroll from 'better-scroll'

export default {
  
  name: 'goods',
  data () {
    return {
      container:{},
      goods:[],
      
    }
  },
  
  //计算属性是不能够接收参数的
  methods:{
    head_bg(imgName){
       return "background-image: url(" + imgName + ");"
    },
    //实现滚动的方法
    // initScroll(){
      
    //                   //$refs 这是一个数组
    //   new BScroll(this.$refs.menuScroll);
    //   new BScroll(this.$refs.foodsScroll);
      
    // }
    
  },
  
  computed:{

  },
  created(){
    fetch("/api/goods")//获取接口
    .then(res=>{
      return res.json()
    })
    .then(response=>{
      
      if(response.code==0){ 
        this.container=response.data.container_operation_source
        this.goods=response.data.food_spu_tags
        // console.log(this.container);
        // console.log(this.goods);
        
      //执行滚动的方法
        // this.initScroll();
      }
    })
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

  .goods{
    display: flex;
    position: absolute;
    top: 190px;
    bottom:51px;
    overflow: hidden;
    width:100%;
    border:1px red solid;
  }
  .goods .menu-wrapper{
    flex: 0 0 85px;  /* 0 0代表你不要动 宽度为 85px */
    background: #f4f4f4;
  }
  .goods .foods-wrapper{
    flex: 1; /*flex 1 代表剩下的宽度都是你的*/
    /* background: yellow;; */
  }

/*menu item 设置专场的样式 */
  .goods .menu-wrapper .menu-item{
   padding:16px 23px 15px 10px;
   border-bottom:1px #e4e4e4 solid;  
  }

  .goods .menu-wrapper .menu-item .text{
    font-size: 12px;
    color:#333333;
    line-height:17px;
    /* 让多出的部分 只显示 2行 和隐藏 */
    -webkit-line-clamp:2;  
    display: -webkit-box;
    -webkit-box-orient: vertical;
    overflow: hidden;

  }
   .goods .menu-wrapper .menu-item .text .icon{
     width:15px;
     height:15px;
     vertical-align:middle;
   }

  /* 专场样式 */
   .goods .foods-wrapper .container-list{
     padding: 10px 11px 0px 11px;
     border-bottom: 1px #e4e4e4 solid;
   }
   .goods .foods-wrapper .container-list img{
     width: 100%;
     margin-bottom: 10px;
     border-radius: 5px;
   }

   /*具体分类样式  */
   .goods .foods-wrapper .food-list{
     padding: 11px;
   }
   .goods .foods-wrapper .food-list .title{
      height: 13px;
      font-size: 13px;
      background: url(img/btn_yellow_highlighted@2x.png) no-repeat left center;
      background-size: 2px 10px;
      padding-left: 7px;
      margin-bottom: 12px;
    }
  .goods .foods-wrapper .food-list .fodd-item{
      display: flex;
      margin-bottom: 25px;
      position: relative;
    }
  .goods .foods-wrapper .food-list .fodd-item  .icon{
    flex: 0 0 63px;
    background-position: center;
    background-size: 120% 100%;
    background-repeat: no-repeat;
    margin-right: 11px;
    height: 75px;
  }
  
  /* 具体商品分类样式 */

.goods .foods-wrapper .food-list .fodd-item .content .name{
	font-size: 16px;
	line-height: 21px;
	color: #333333;
	margin-bottom: 10px;
	padding-right: 27px;
}
.goods .foods-wrapper .food-list .fodd-item .content .desc{
	font-size: 10px;
	line-height: 19px;
	color: #bfbfbf;
	margin-bottom: 8px;
	
	/* 超出部分显示省略号*/
	-webkit-line-clamp: 1;
	display: -webkit-box;
	-webkit-box-orient: vertical;
	overflow: hidden;
}
.goods .foods-wrapper .food-list .fodd-item .content .extra{
	font-size: 10px;
	color: #BFBFBF;
	margin-bottom: 7px;
}
.goods .foods-wrapper .food-list .fodd-item .content .extra .saled{
	margin-right: 14px;
}
.goods .foods-wrapper .food-list .fodd-item .content .product{
	height: 15px;
	margin-bottom: 6px;
}
.goods .foods-wrapper .food-list .fodd-item .content .price{
	font-size: 0;
}
.goods .foods-wrapper .food-list .fodd-item .content .price .text{
	font-size: 14px;
	color: #fb4e44;
}
.goods .foods-wrapper .food-list .fodd-item .content .price .unit{
	font-size: 12px;
	color: #BFBFBF;
}


</style>



