<template>
    <div class="star">
        <span class="star-item" v-for="(itemClass,index) in itemClasses" :key="index" :class="itemClass"></span>
        <!--绑定了一个动态的class -->
    </div>
</template>
<script>
// 星星的长度
    const LENGTH = 5
//星星的状态
    const CLS_ON='on'
    const CLS_HALF='half'
    const CLS_OFF='off'

export default {
    props:{
        score:{//这是属性是 在header里面的星级评价绑定的
            type:Number
        }
    },
    computed:{
        itemClasses(){
            let reset=[]

            //如果说 是4.7 那么就会把他向下取整
            let score=Math.floor(this.score*2)/2;
            
            //如果是半星 哪么就有小数点 4.7 
            let hasd=score % 1 !== 0

            // 如果是全星
            let intgt=Math.floor(score)

            //遍历拥有几个星星
            for(let i = 0; i < intgt; i++){
                reset.push(CLS_ON)
            }

            //处理半星
            if(hasd){
                reset.push(CLS_HALF)
            }

            //补齐全星
            while(reset.length<LENGTH){
                reset.push(CLS_OFF)

            }
            return reset
        }
    }
}
</script>
<style scoped>
    .star{
		font-size: 0;
	}
	.star .star-item{
		display: inline-block;
		width: 10px;
		height: 10px;
		margin-right: 3px;
		background-repeat: no-repeat;
		background-size: 10px 10px;
	}
	.star .star-item:last-child{
		margin-right: 0;
	}
	
	/* 三种图片类型*/
   .star .on{
		background-image: url(img/star24_on@2x.png);
	}
	.star .half{
		background-image: url(img/star24_half@2x.png);
	}
	.star .off{
		background-image: url(img/star24_off@2x.png);
	}
</style>


