<template>
  <div class="nav">
    <router-link class="nav-item" to="/goods">
    点餐
    <i class="line"></i>
    </router-link>
    <router-link class="nav-item" to="/ratings">
    评价
    <i class="line"></i>
    </router-link>
    <router-link class="nav-item" to="/seller">
    商家
    <i class="line"></i>
    </router-link>
    
  </div>
</template>

<script>
export default {
  name: 'nav',
  data () {
    return {
      
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.nav{
    display: flex;
    widows: 100%;
    height: 40px;
    line-height: 40px;
    border-bottom: 1px solid royalblue;
   
}
.nav-item{
    flex:1;
    text-align: center;
     position: relative;
}
/*默认选中样式*/
.nav .active{
    color: rgb(107, 1, 246);

}
.nav .active .line{
  width:20px;
  height: 2px;
  background:yellow;
  display: inline-block;
  position: absolute;
  bottom: 0px;
  left:50%;  
  margin-left: -10px; 
}
</style>



